.navbar {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    /* padding: 1rem; */
    position: fixed;
    width: 100%;
    min-height: 100px;
    height: 150px;
    background-color: rgb(50 69 96);
    /* background-color: rgb(10, 25, 47); */

    color: beige;
    border-bottom: 2px solid white;
    z-index: 8;
}

.nav-link:hover {
    color: rgb(250, 215, 127)
}


.lion {
    width: 140px;
    margin-left: 2rem;
}

@media(max-width:767px) {
    .lion {
        width: 100px;
        margin-left: 2rem;
    }
}


.lion:hover {
    animation: rotate-90-cw .4s cubic-bezier(.25, .46, .45, .94) both;
    animation: slide-right 1s ease 0s 1 normal none;

}

@keyframes slide-right {

    0% {
        transform: translateX(0);
    }

    50% {
        transform: translateX(50px);
    }

    75% {
        transform: translateX(50px);
    }

    100% {
        transform: translateX(0);
    }
}

/* @media(max-width:767px) {
    .lion {
        width: rem;
    }
} */

.navbar__links {
    display: flex;
    justify-content: center;
    align-items: center;
    padding-right: 1rem;

}

.navbar__link {
    padding: 2rem;
    color: inherit;
    font-size: 25px;
}

.navbar__burger {
    width: 40px;
    height: 40px;
    background: transparent;
    border: none;
    color: inherit;
    display: none;
}

@media (max-width:768px) {
    .navbar__links {
        flex-direction: column;
        justify-content: center;
        align-items: center;
        position: fixed;
        right: -100vw;
        bottom: 0;
        width: 0;
        height: 100vh;
        padding: 2rem;
        visibility: hidden;
        background-color: black;
        transition: all 0.8s ease-out;
    }

    .show-nav .navbar__links {
        width: 100vw;
        right: 0;
        visibility: visible;
    }

    /* bonus */
    .navbar__item::after {
        content: "";
        display: block;
        margin: 0 auto;
        width: 3vw;
        height: 1px;
        background: white;
    }

    .navbar__item:last-child::after {
        display: none;
    }


    .navbar__link {
        display: block;
        padding: 1.5rem;
        font-size: 5vw;
    }

    .navbar__burger {
        display: block;
        position: fixed;
        /* top: 1rem; */
        right: 1rem;
    }

    .navbar__burger:hover {
        cursor: pointer;
    }

    .burger-bar,
    .burger-bar::before,
    .burger-bar::after {
        display: block;
        width: 40px;
        height: 3px;
        position: relative;
        border-radius: 3px;
        background-color: aliceblue;
        transition: all .4s ease-in-out;
    }

    .burger-bar::before,
    .burger-bar::after {
        content: "";
        position: absolute;
        left: 0;
    }

    .burger-bar::before {
        transform: translateY(-12px);
    }

    .burger-bar::after {
        transform: translateY(12px);

    }

    .show-nav .burger-bar {
        width: 0;
        background: transparent;
    }

    .show-nav .burger-bar::before {
        transform: rotate(45deg);
    }

    .show-nav .burger-bar::after {
        transform: rotate(-45deg);
    }

    /* burger button animation */
    .navbar__item {
        transform: translateY(100vh);
    }

    .show-nav .navbar__item {
        transform: translateY(0);
    }

    .show-nav .slideInDown-1 {
        transition: all 0.8s ease-out;
    }

    .show-nav .slideInDown-2 {
        transition: all 0.9s ease-out;
    }

    .show-nav .slideInDown-3 {
        transition: all 1s ease-out;
    }

    .show-nav .slideInDown-4 {
        transition: all 1.2s ease-out;
    }

    .show-nav .slideInDown-5 {
        transition: all 1.3s ease-out;
    }

}

.btn {
    background-color: rgb(98 128 153);
    padding: 0.8rem;
    transition-duration: 0.4s;
    /* border: 2px solid rgb(250, 215, 127); */
    border: 2px solid white;
    border-radius: 0.6rem;
    font-size: 25px;
    color: rgb(250, 215, 127)
}

.btn:hover {
    background-color: rgb(250, 215, 127);
    /* Green */
    color: white;
    cursor: pointer;
}

@media(max-width:360px) {
    .show-nav .navbar__links {
        width: 113vw;
        height: 115vh;
    }
}

@media(max-width:300px) {
    .show-nav .navbar__links {
        width: 125vw;
        height: 128vh;
    }
}

.effet:hover {
    animation: text-shadow-drop-down 1s ease 0s 1 normal forwards;
}